<template>
  <main class="content">
    <div class="container-fluid p-0">
      <div class="page-title-right">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item">
            <a href="javascript: void(0);">Anasayfa</a>
          </li>
          <li class="breadcrumb-item active">Projeekle</li>
        </ol>
      </div>
      <br />
      <h1 class="h3 mb-3">Proje Ekle</h1>
      <div class="row">
        <div class="col-12">
          <div class="card">
            <div class="card-body">
              <div class="row">
                <div class="col-xl-6">
                  <div class="form-group">
                    <label for="projectname">İsim</label>
                    <input
                      type="text"
                      id="projectname"
                      class="form-control"
                      placeholder="Projenin adını giriniz"
                      v-model="isim"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="projectcode">Proje Stok kodu</label>
                    <input
                      type="text"
                      id="projectcode"
                      class="form-control"
                      placeholder="Projenin kodunu giriniz"
                      v-model="projekodu"
                      required
                    />
                  </div>

                  <div class="form-group">
                    <label for="project-overview">Detay</label>
                    <textarea
                      class="form-control"
                      id="project_overview"
                      rows="5"
                      placeholder="Proje hakkında birkaç detay"
                       v-model="detay"
                       required
                    ></textarea>
                  </div>

                  <!-- Date View -->
                  <div class="form-group">
                    <label>Başlama Tarihi</label>
                   <datepicker  id="begindate"  :format="format"  bootstrap-styling   v-model="begintime"> </datepicker>
                  </div>
                  <!-- Date View -->
                  <div class="form-group">
                    <label>Bitiş Tarihi</label>
                    <datepicker  id="enddate" :format="format"  bootstrap-styling   v-model="endtime" > </datepicker>
                  </div>
                  <br />
                  <br />
                </div>
                <!-- end col-->
              </div>
              <!-- end row -->
              <button class="btn btn-primary" type="button" id="addproject" @click="kaydet">Kaydet</button>
            </div>
            <!-- end card-body -->
          </div>
          <!-- end card-->
        </div>
        <!-- end col-->
      </div>
    </div>
  </main>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
 import Swal from "sweetalert2";
import  state from "../../Helper/custom_state";
export default {
  
  data() {
    return {
      isim: "",
      projekodu: "",
      detay:"",
      begintime:"",
      endtime:"",
      format:"yyyy-MM-dd"
    };
  },
  methods: {
    kaydet() {
                    // eslint-disable-next-line no-console
           //console.log(this.begintime.getFullYear()+"/"+this.begintime.getMonth()+"/"+this.begintime.getDate());
            this.$http.post(`${state.ip}/api/admin/addproject`, {
             name:this.isim ,
              project: this.projekodu,
              detay:this.detay,
              begintime:this.begintime,
              endtime:this.endtime
            
          }).then(response => {
              // eslint-disable-next-line no-console
              console.log(response);
               Swal.fire("", response.data.msg, "success");
              this.isim ="",
              this.projekodu="",
              this.detay="",
              this.begintime="",
              this.endtime=""
              
            })
            .catch(function(error) {
              // eslint-disable-next-line no-console
              console.log(error.response);
            });
  


    }
  
  },
   
  // ...
  components: {
    Datepicker
  }
  // ...
}
</script>

<style>
</style>